import ProductCard from "@/app/components/After_log_homepage/ProductCard";
import { useParams } from "next/navigation";


const sampleProducts = [
  { id: "1", image: "HarryProject/public/1.jpg", name: "Casual Dress", price: "49.99", description: "Casual wear", category: "dresses", subcategory: "Casual" },
  { id: "2", image: "HarryProject/public/1.jpg", name: "Formal Shirt", price: "39.99", description: "Formal style", category: "shirts", subcategory: "Formal" },
];

export default function SubcategoryPage() {
  const params = useParams();
  const category = params?.category as string;
  const subcategory = params?.subcategory as string;

  const filteredProducts = sampleProducts.filter((p) => p.category === category && p.subcategory.toLowerCase() === subcategory);

  return (
    <div className="container mx-auto px-4">
      <h1 className="text-2xl font-bold mb-4">
        {category.toUpperCase()} - {subcategory.toUpperCase()}
      </h1>

      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4">
        {filteredProducts.length > 0 ? (
          filteredProducts.map((product) => <ProductCard key={product.id} product={product} />)
        ) : (
          <p>No products found in this subcategory.</p>
        )}
      </div>
    </div>
  );
}
